import React from "react";
import { Select, Row, Col } from "antd";
import { AutoComplete } from 'antd';


import "antd/dist/antd.css";


const SearchBar = (props) => {

    const { Option } = Select;

const optionsSelect = [
    { value: 1, label: "Data set 1" },
    { value: 2, label: "Data set 2" },
    { value: 3, label: "Data set 3" }
];

const optionsAutoComplete = [
    { value: 'Disponibilidad de aulas' },
    { value: 'Asignaturas por edificio' },
    { value: 'Cantidad de creditos por carrera' },
    { value: 'Cantidad de profesores por carrera' },
    { value: 'Cantidad de profesores con maestria' },
    { value: 'Disponibilidad de laboratorios' },
];

const AutoCom = () => (
    <AutoComplete
        style={{
            width: 300,
        }}
        options={optionsAutoComplete}
        placeholder="Ingresa lo que quieres encontrar"
        filterOption={(inputValue, option) =>
            option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
        }
    />
);
const SelectComponent = () => {

    return (
        <Select style={{ width: 200 }} defaultValue={props.dataset} onChange={(value)=>{
            props.setDatasetFunction(value)
        }}>
            {optionsSelect.map(opt => (
                <Option  key={opt.value} value={opt.value} >
                    {opt.label}
                </Option>
            ))}
        </Select>
    );

}



    return (
        <>
            <Col xs={12} sm={12} md={12} lg={12} xl={12}>
                <Row justify='center' style={{
                    padding: '10px'
                }}>
                    <h4 style={{
                        marginTop: '5px',
                        marginRight: '10px'
                    }}>Busqueda: </h4>
                    <SelectComponent />
                </Row>
            </Col>
            <Col xs={12} sm={12} md={12} lg={12} xl={12}>
                <Row justify='center' style={{
                    padding: '10px'
                }}>
                    <h4 style={{
                        marginTop: '5px',
                        marginRight: '10px'
                    }}>Busqueda avanzada: </h4>
                    <AutoCom />
                </Row>
            </Col>
        </>
    )
}

export default SearchBar;
