import React from 'react';

import { Form, Input, Button, Checkbox }  from 'antd';
import {UserOutlined , LockOutlined } from '@ant-design/icons';
import { Row } from 'antd';
import 'antd/dist/antd.css';
import { Card } from 'antd';

const FormItem = Form.Item;

const Formulario = (props)=>{
 
    return (
      <Form className="login-form" style={{
        
      }}>

        <FormItem>
     
            <Input prefix={<UserOutlined />} 
             placeholder="Usuario" />
            
        </FormItem>

        <FormItem>

            <Input prefix={<LockOutlined />} 
             type="password" placeholder="Contraseña" />
     
        </FormItem>
        
        <FormItem>
        
            <Checkbox>Recordarme</Checkbox>
          
          <Button type="primary" htmlType="submit" className="login-form-button" style={{marginTop: '10px'}}>
            Ingresar
          </Button>
          <p className="login-form-forgot" style={{marginTop: '10px', fontSize: '11px'}}>Olvide mi contraseña</p>
        </FormItem>
      </Form>
    );
  
}

const CardLogin = () =>{
return(
  <Row justify="center">
    <Card style={{ width: 250, marginBottom: '20px', backgroundColor: '#B6C1BE'}}>
      <Formulario/>
    </Card>
  </Row>
)};

export default CardLogin;
