import React from 'react';
import Slider from 'infinite-react-carousel';
import { Row, Col } from 'antd';

const SliderView = () => {

  const settings = {
    autoplay: true,
    dots: true,
  };

  return (

    <Row>
      <Col xs={24} sm={24} md={24} lg={24} xl={24}>
        <div>
          <Slider {...settings}>
            <div>
              <div>
                <img src="img/banner/banner_tyr.png" alt="Smiley face" style={{  width: '100%'  }} />
              </div>
            </div>
            <div>
              {/* <img src="http://extension.unal.edu.co/fileadmin/recursos/siun/convocatorias/2019-2021/img/extension-solidaria-unal-2020-banner.png" style={{  width: '100%' }} alt="Smiley face" /> */}
              <img src="img/banner/banner_tyr.png" alt="Smiley face" style={{  width: '100%'  }} />
            </div>
            <div>
              {/* <img src="http://extension.unal.edu.co/fileadmin/recursos/propiedad_intelectual/banner/banner_seminario_pi_1440x320.png" style={{maxWidth: '1350'}}alt="Smiley face" /> */}
              <img src="img/banner/banner_tyr.png" alt="Smiley face" style={{  width: '100%'  }} />
            </div>
          </Slider>
        </div>
      </Col>
    </Row>
  );
}
export default SliderView;


