import React from 'react'
import { ResponsiveBar } from '@nivo/bar'
// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.
const MyBar = () => {
    const data = [
        {
          "country": "AD",
          "hot dog": 46,
          "hot dogColor": "hsl(34, 70%, 50%)",
          "burger": 173,
          "burgerColor": "hsl(82, 70%, 50%)",
          "sandwich": 119,
          "sandwichColor": "hsl(222, 70%, 50%)",
          "kebab": 57,
          "kebabColor": "hsl(70, 70%, 50%)",
          "fries": 102,
          "friesColor": "hsl(85, 70%, 50%)",
          "donut": 50,
          "donutColor": "hsl(24, 70%, 50%)"
        },
        {
          "country": "AE",
          "hot dog": 186,
          "hot dogColor": "hsl(356, 70%, 50%)",
          "burger": 128,
          "burgerColor": "hsl(254, 70%, 50%)",
          "sandwich": 29,
          "sandwichColor": "hsl(10, 70%, 50%)",
          "kebab": 6,
          "kebabColor": "hsl(33, 70%, 50%)",
          "fries": 144,
          "friesColor": "hsl(32, 70%, 50%)",
          "donut": 130,
          "donutColor": "hsl(27, 70%, 50%)"
        },
        {
          "country": "AF",
          "hot dog": 82,
          "hot dogColor": "hsl(105, 70%, 50%)",
          "burger": 142,
          "burgerColor": "hsl(207, 70%, 50%)",
          "sandwich": 133,
          "sandwichColor": "hsl(115, 70%, 50%)",
          "kebab": 28,
          "kebabColor": "hsl(22, 70%, 50%)",
          "fries": 181,
          "friesColor": "hsl(266, 70%, 50%)",
          "donut": 129,
          "donutColor": "hsl(347, 70%, 50%)"
        },
        {
          "country": "AG",
          "hot dog": 14,
          "hot dogColor": "hsl(303, 70%, 50%)",
          "burger": 85,
          "burgerColor": "hsl(109, 70%, 50%)",
          "sandwich": 139,
          "sandwichColor": "hsl(4, 70%, 50%)",
          "kebab": 61,
          "kebabColor": "hsl(60, 70%, 50%)",
          "fries": 142,
          "friesColor": "hsl(232, 70%, 50%)",
          "donut": 142,
          "donutColor": "hsl(114, 70%, 50%)"
        },
        {
          "country": "AI",
          "hot dog": 82,
          "hot dogColor": "hsl(351, 70%, 50%)",
          "burger": 107,
          "burgerColor": "hsl(325, 70%, 50%)",
          "sandwich": 139,
          "sandwichColor": "hsl(2, 70%, 50%)",
          "kebab": 195,
          "kebabColor": "hsl(193, 70%, 50%)",
          "fries": 198,
          "friesColor": "hsl(4, 70%, 50%)",
          "donut": 170,
          "donutColor": "hsl(57, 70%, 50%)"
        },
        {
          "country": "AL",
          "hot dog": 48,
          "hot dogColor": "hsl(316, 70%, 50%)",
          "burger": 140,
          "burgerColor": "hsl(87, 70%, 50%)",
          "sandwich": 171,
          "sandwichColor": "hsl(279, 70%, 50%)",
          "kebab": 135,
          "kebabColor": "hsl(157, 70%, 50%)",
          "fries": 36,
          "friesColor": "hsl(196, 70%, 50%)",
          "donut": 1,
          "donutColor": "hsl(19, 70%, 50%)"
        },
        {
          "country": "AM",
          "hot dog": 160,
          "hot dogColor": "hsl(167, 70%, 50%)",
          "burger": 117,
          "burgerColor": "hsl(321, 70%, 50%)",
          "sandwich": 109,
          "sandwichColor": "hsl(343, 70%, 50%)",
          "kebab": 16,
          "kebabColor": "hsl(176, 70%, 50%)",
          "fries": 140,
          "friesColor": "hsl(49, 70%, 50%)",
          "donut": 90,
          "donutColor": "hsl(93, 70%, 50%)"
        }
      ]
    return (
    <ResponsiveBar
        data={data}
        keys={[ 'hot dog', 'burger', 'sandwich', 'kebab', 'fries', 'donut' ]}
        indexBy="country"
        margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
        padding={0.3}
        colors={{ scheme: 'nivo' }}
        defs={[
            {
                id: 'dots',
                type: 'patternDots',
                background: 'inherit',
                color: '#38bcb2',
                size: 4,
                padding: 1,
                stagger: true
            },
            {
                id: 'lines',
                type: 'patternLines',
                background: 'inherit',
                color: '#eed312',
                rotation: -45,
                lineWidth: 6,
                spacing: 10
            }
        ]}
        fill={[
            {
                match: {
                    id: 'fries'
                },
                id: 'dots'
            },
            {
                match: {
                    id: 'sandwich'
                },
                id: 'lines'
            }
        ]}
        borderColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
        axisTop={null}
        axisRight={null}
        axisBottom={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: 'country',
            legendPosition: 'middle',
            legendOffset: 32
        }}
        axisLeft={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: 'food',
            legendPosition: 'middle',
            legendOffset: -40
        }}
        labelSkipWidth={12}
        labelSkipHeight={12}
        labelTextColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
        legends={[
            {
                dataFrom: 'keys',
                anchor: 'bottom-right',
                direction: 'column',
                justify: false,
                translateX: 120,
                translateY: 0,
                itemsSpacing: 2,
                itemWidth: 100,
                itemHeight: 20,
                itemDirection: 'left-to-right',
                itemOpacity: 0.85,
                symbolSize: 20,
                effects: [
                    {
                        on: 'hover',
                        style: {
                            itemOpacity: 1
                        }
                    }
                ]
            }
        ]}
        animate={true}
        motionStiffness={90}
        motionDamping={15}
    />
)}

export default MyBar;